import api from '../../common/request.js'

export default {
	data() {
		return {
			userId: '',
			bookId: '',
			book: '',
			likelist: [],
			openStatusBtn: false,
			isBookShelf: false
		}
	},
	onLoad(option) {
		this.userId = uni.getStorageSync('__userid__') || ''
		this.bookId = option.id
		this.getBookData(this.bookId)
		this.getLikeBookData(this.bookId)
		// this.getIsBookShelf()
	},
	onShow() {
		this.getIsBookShelf()
	},
	onReady() {
		
	},
	methods: {
		// 字数转换
		wordsCount(num) {
			if (num > 10000) {
			  return Math.round(num / 10000) + '万字'
			} else {
			  return num + '字'
			}
		},
		// 展开
		openAndCloseBtn() {
			this.openStatusBtn = !this.openStatusBtn
		},
		// 添加书架
		addBookShelf() {
		  if (this.userId) {
		    let para = {
		      user_id: this.userId,
		      book_id: this.bookId
		    }
		    api.post('/user/addUserCase', para).then(res => {
		      if (res.code === 200) {
						this.isBookShelf = true
		      } else {
						this.isBookShelf = false
		      }
		    })
		  } else {
				// uni.switchTab({
				//   url: '../user/user'
				// })
				
		    uni.showToast({
		      title: '请先登录',
		      icon: 'none',
		      duration: 2000
		    })
		  }
		},
		// 移出书架
		deleteBookShelf() {
		  let para = {
		    user_id: this.userId,
		    book_id: this.bookId
		  }
		  api.post('/user/deleteUserCase', para).then(res => {
		    if (res.code === 200) {
				this.isBookShelf = false
		    } else {
				this.isBookShelf = false
		    }
		  })
		},
		getIsBookShelf() {
		  let para = {
		    user_id: this.userId,
		    book_id: this.bookId
		  }
		  api.post('/book/bookIsCase', para).then(res => {
		    if (res.code === 200) {
				let status = res.data.is_case === 'Y' ? true : false
				this.isBookShelf = status
		    }
		  })
		},
		gotoRead() {
		  // let title = this.data.book.book_title
		  uni.setStorageSync('__bookid__', this.bookId)
		  uni.setStorageSync('__sectionid__', 0)
		  uni.navigateTo({
		    // url: `../read/read?title=${title}`
		    url: `../read/read`
		  })
		},
		getBookData(id) {
		  api.post('/book/detail', {book_id: id}).then(res => {
		    if (res.code === 200) {
				this.book = res.data
				uni.setNavigationBarTitle({
					title: res.data.book_title
				})
		    }
		  })
		},
		getLikeBookData(id) {
		  let para = {
		    book_id: id,
		    page: 1,
		    limit: 6
		  }
		  api.post('/book/likeBook', para).then(res => {
		    if (res.code === 200) {
				this.likelist = res.data
		    }
		  })
		}
	}
}
